// frontend/src/components/ServerThemesPanel.js
import { useState, useEffect } from 'react';
import { FaTimes, FaPalette, FaPlus, FaCheck } from 'react-icons/fa';
import toast from '../utils/toast';

/**
 * ðŸŽ¨ Server Themes Panel
 * Sunucu tema Ã¶zelleÅŸtirme
 */

const ServerThemesPanel = ({ fetchWithAuth, apiBaseUrl, serverId, onClose }) => {
    const [themes, setThemes] = useState([]);
    const [activeTheme, setActiveTheme] = useState(null);
    const [customTheme, setCustomTheme] = useState({
        name: '',
        primary_color: '#5865f2',
        secondary_color: '#2c2f33',
        background_color: '#1e1e1e',
        text_color: '#ffffff',
        accent_color: '#43b581'
    });
    const [loading, setLoading] = useState(true);

    const presetThemes = [
        {
            name: 'Discord Classic',
            primary_color: '#5865f2',
            secondary_color: '#2c2f33',
            background_color: '#1e1e1e',
            text_color: '#ffffff',
            accent_color: '#43b581'
        },
        {
            name: 'Dark Purple',
            primary_color: '#7289da',
            secondary_color: '#2c2f33',
            background_color: '#23272a',
            text_color: '#ffffff',
            accent_color: '#99aab5'
        },
        {
            name: 'Ocean Blue',
            primary_color: '#4a9eff',
            secondary_color: '#1a3a52',
            background_color: '#0d2137',
            text_color: '#e3f2fd',
            accent_color: '#00bcd4'
        },
        {
            name: 'Forest Green',
            primary_color: '#4caf50',
            secondary_color: '#1b5e20',
            background_color: '#0d3b0d',
            text_color: '#e8f5e9',
            accent_color: '#8bc34a'
        },
        {
            name: 'Sunset Orange',
            primary_color: '#ff9800',
            secondary_color: '#e65100',
            background_color: '#3e2723',
            text_color: '#fff3e0',
            accent_color: '#ffb74d'
        },
        {
            name: 'Midnight Black',
            primary_color: '#424242',
            secondary_color: '#212121',
            background_color: '#000000',
            text_color: '#e0e0e0',
            accent_color: '#757575'
        }
    ];

    useEffect(() => {
        loadThemes();
    }, []);

    const loadThemes = async () => {
        try {
            setLoading(true);
            const response = await fetchWithAuth(`${apiBaseUrl}/servers/${serverId}/themes/`);
            if (response.ok) {
                const data = await response.json();
                setThemes(data.themes || []);
                setActiveTheme(data.active_theme);
            }
        } catch (error) {
            console.error('Tema yÃ¼kleme hatasÄ±:', error);
        } finally {
            setLoading(false);
        }
    };

    const applyTheme = async (theme) => {
        try {
            const response = await fetchWithAuth(`${apiBaseUrl}/servers/${serverId}/theme/apply/`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(theme)
            });

            if (response.ok) {
                toast.success('Tema uygulandÄ±');
                setActiveTheme(theme);
            } else {
                toast.error('Tema uygulanamadÄ±');
            }
        } catch (error) {
            console.error('Tema uygulama hatasÄ±:', error);
            toast.error('Bir hata oluÅŸtu');
        }
    };

    const saveCustomTheme = async () => {
        if (!customTheme.name.trim()) {
            toast.error('Tema adÄ± girin');
            return;
        }

        try {
            const response = await fetchWithAuth(`${apiBaseUrl}/servers/${serverId}/theme/save/`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(customTheme)
            });

            if (response.ok) {
                toast.success('Ã–zel tema kaydedildi');
                loadThemes();
                setCustomTheme({
                    name: '',
                    primary_color: '#5865f2',
                    secondary_color: '#2c2f33',
                    background_color: '#1e1e1e',
                    text_color: '#ffffff',
                    accent_color: '#43b581'
                });
            } else {
                toast.error('Tema kaydedilemedi');
            }
        } catch (error) {
            console.error('Tema kaydetme hatasÄ±:', error);
            toast.error('Bir hata oluÅŸtu');
        }
    };

    return (
        <div style={styles.overlay} onClick={onClose}>
            <div style={styles.modal} onClick={(e) => e.stopPropagation()}>
                <div style={styles.header}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                        <FaPalette style={{ color: '#5865f2' }} />
                        <h2 style={{ margin: 0 }}>Sunucu TemalarÄ±</h2>
                    </div>
                    <FaTimes onClick={onClose} style={styles.closeBtn} />
                </div>

                <div style={styles.content}>
                    <section style={styles.section}>
                        <h3 style={styles.sectionTitle}>HazÄ±r Temalar</h3>
                        <div style={styles.themeGrid}>
                            {presetThemes.map((theme, index) => (
                                <div
                                    key={index}
                                    style={styles.themeCard}
                                    onClick={() => applyTheme(theme)}
                                >
                                    <div style={styles.themePreview}>
                                        <div style={{ ...styles.colorStrip, backgroundColor: theme.background_color }} />
                                        <div style={{ ...styles.colorStrip, backgroundColor: theme.secondary_color }} />
                                        <div style={{ ...styles.colorStrip, backgroundColor: theme.primary_color }} />
                                        <div style={{ ...styles.colorStrip, backgroundColor: theme.accent_color }} />
                                    </div>
                                    <div style={styles.themeName}>{theme.name}</div>
                                    {activeTheme?.name === theme.name && (
                                        <FaCheck style={styles.activeCheck} />
                                    )}
                                </div>
                            ))}
                        </div>
                    </section>

                    <section style={styles.section}>
                        <h3 style={styles.sectionTitle}>Ã–zel Tema OluÅŸtur</h3>
                        <div style={styles.customForm}>
                            <input
                                type="text"
                                placeholder="Tema AdÄ±"
                                value={customTheme.name}
                                onChange={(e) => setCustomTheme({ ...customTheme, name: e.target.value })}
                                style={styles.input}
                            />
                            <div style={styles.colorGrid}>
                                <div style={styles.colorField}>
                                    <label>Ana Renk</label>
                                    <input
                                        type="color"
                                        value={customTheme.primary_color}
                                        onChange={(e) => setCustomTheme({ ...customTheme, primary_color: e.target.value })}
                                        style={styles.colorInput}
                                    />
                                </div>
                                <div style={styles.colorField}>
                                    <label>Ä°kinci Renk</label>
                                    <input
                                        type="color"
                                        value={customTheme.secondary_color}
                                        onChange={(e) => setCustomTheme({ ...customTheme, secondary_color: e.target.value })}
                                        style={styles.colorInput}
                                    />
                                </div>
                                <div style={styles.colorField}>
                                    <label>Arkaplan</label>
                                    <input
                                        type="color"
                                        value={customTheme.background_color}
                                        onChange={(e) => setCustomTheme({ ...customTheme, background_color: e.target.value })}
                                        style={styles.colorInput}
                                    />
                                </div>
                                <div style={styles.colorField}>
                                    <label>Metin</label>
                                    <input
                                        type="color"
                                        value={customTheme.text_color}
                                        onChange={(e) => setCustomTheme({ ...customTheme, text_color: e.target.value })}
                                        style={styles.colorInput}
                                    />
                                </div>
                                <div style={styles.colorField}>
                                    <label>Vurgu</label>
                                    <input
                                        type="color"
                                        value={customTheme.accent_color}
                                        onChange={(e) => setCustomTheme({ ...customTheme, accent_color: e.target.value })}
                                        style={styles.colorInput}
                                    />
                                </div>
                            </div>
                            <button onClick={saveCustomTheme} style={styles.saveBtn}>
                                <FaPlus /> TemayÄ± Kaydet
                            </button>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    );
};

const styles = {
    overlay: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0,0,0,0.7)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 999999
    },
    modal: {
        backgroundColor: '#1e1e1e',
        borderRadius: '8px',
        width: '90%',
        maxWidth: '900px',
        maxHeight: '90vh',
        display: 'flex',
        flexDirection: 'column',
        color: '#fff'
    },
    header: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '20px',
        borderBottom: '1px solid #333'
    },
    closeBtn: {
        cursor: 'pointer',
        fontSize: '24px',
        color: '#888'
    },
    content: {
        flex: 1,
        overflowY: 'auto',
        padding: '20px'
    },
    section: {
        marginBottom: '30px'
    },
    sectionTitle: {
        color: '#dcddde',
        fontSize: '18px',
        fontWeight: '600',
        marginBottom: '15px'
    },
    themeGrid: {
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fill, minmax(200px, 1fr))',
        gap: '15px'
    },
    themeCard: {
        backgroundColor: '#2c2f33',
        borderRadius: '8px',
        padding: '15px',
        cursor: 'pointer',
        position: 'relative',
        transition: 'transform 0.2s'
    },
    themePreview: {
        display: 'flex',
        height: '60px',
        borderRadius: '4px',
        overflow: 'hidden',
        marginBottom: '10px'
    },
    colorStrip: {
        flex: 1
    },
    themeName: {
        textAlign: 'center',
        fontSize: '14px',
        fontWeight: '500'
    },
    activeCheck: {
        position: 'absolute',
        top: '10px',
        right: '10px',
        color: '#43b581',
        fontSize: '20px'
    },
    customForm: {
        backgroundColor: '#2c2f33',
        borderRadius: '8px',
        padding: '20px'
    },
    input: {
        width: '100%',
        padding: '10px',
        backgroundColor: '#1e1e1e',
        border: '1px solid #444',
        borderRadius: '4px',
        color: '#fff',
        fontSize: '14px',
        marginBottom: '15px'
    },
    colorGrid: {
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fit, minmax(120px, 1fr))',
        gap: '15px',
        marginBottom: '15px'
    },
    colorField: {
        display: 'flex',
        flexDirection: 'column',
        gap: '8px'
    },
    colorInput: {
        width: '100%',
        height: '50px',
        border: 'none',
        borderRadius: '4px',
        cursor: 'pointer'
    },
    saveBtn: {
        width: '100%',
        backgroundColor: '#5865f2',
        color: '#fff',
        border: 'none',
        padding: '12px',
        borderRadius: '4px',
        cursor: 'pointer',
        fontSize: '16px',
        fontWeight: '600',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        gap: '8px'
    }
};

export default ServerThemesPanel;
